.form {

    display: flex;
    flex-direction: column;
    max-width: 40%;
    margin: 0 auto;
    row-gap: 10px;
    align-items: center;
    padding: 20px;
}

.form input {
    padding: 7px;
    border-radius: 5px;
    width: 100%;
}

.form select {
    padding: 7px;
    border-radius: 5px;
    width: 50%;
    background-color: white;
}

.form button {
    padding: 7px;
    border-radius: 5px;
    width: 50%;
    background-color: white;
}