.category {
    width: 1000px;
    margin: 0 auto;
    text-align: center;
    font-size: 18px;
    border-collapse: collapse;
    font-weight: 500;
}

.category caption {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 15px;
}

.category td {
    border: 1px solid;
    height: 60px;
    width: 25%;
    max-width: 300px;
    overflow-wrap: break-word;
    padding: 10px;
}