@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif;
}

.App {
  position: relative;
  text-align: center;
  background-color: rgb(67, 126, 87);
  min-height: 100vh;
  color: rgb(31, 31, 31);
  padding: 65px 30px;
}

.change_button {
  display: block;
  width: fit-content;
  margin-left: auto;
  background-color: rgb(243, 243, 243);
  border: none;
  padding: 10px;
  border-radius: 20px;
  transition: 0.2s;
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 15px;
}

.change_button:hover {
  background-color: #4096ff;
  color: rgb(243, 243, 243);
}